.cutlist-editor {
  flex-flow: row;
  display: flex;
}

.canvas-container {
  flex-flow: column;
  flex: 1;
  display: flex;
}

* {
  box-sizing: border-box;
}

:root {
  --primary-color: #007bff;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

main {
  margin: .5rem;
}

button {
  background: var(--primary-color);
  color: #fff;
  border: solid 1px var(--primary-color);
  cursor: pointer;
  border-radius: .25rem;
  padding: .5rem 1rem;
  font-size: 1rem;
}

button:hover {
  background: color-mix(in srgb, var(--primary-color) 100%, black 20%);
}

input, select {
  border: 1px solid #ccc;
  border-radius: .25rem;
  padding: .5rem;
  font-size: 1rem;
}

.ww-input-layout {
  flex-flow: row;
  align-items: center;
  gap: .5rem;
  display: flex;
}

.ww-input-layout label {
  min-width: 100px;
  display: inline-block;
}

.sides-editor {
  display: block;
  position: relative;
}

.sides-editor .left {
  height: calc(100% - 20px);
  position: absolute;
  top: 10px;
  left: 0;
}

.hstack {
  flex-direction: row;
  align-items: center;
  display: flex;
}

.vstack {
  flex-direction: column;
  justify-content: start;
  display: flex;
}

.p-0 {
  padding: 0;
}

.m-0 {
  margin: 0;
}

.gap-0 {
  gap: 0;
}

.p-1 {
  padding: .25rem;
}

.m-1 {
  margin: .25rem;
}

.gap-1 {
  gap: .25rem;
}

.p-2 {
  padding: .5rem;
}

.m-2 {
  margin: .5rem;
}

.gap-2 {
  gap: .5rem;
}

.p-3 {
  padding: .75rem;
}

.m-3 {
  margin: .75rem;
}

.gap-3 {
  gap: .75rem;
}

.p-4 {
  padding: 1rem;
}

.m-4 {
  margin: 1rem;
}

.gap-4 {
  gap: 1rem;
}

.flex-grow {
  flex-grow: 1;
}

.w-100 {
  width: 100%;
}

.ww-nabbar {
  background: var(--primary-color);
  flex-flow: row;
  display: flex;
}

.ww-nabbar a {
  color: #fff;
  padding: .5rem 1rem;
  text-decoration: none;
}

.ww-nabbar a.--active {
  font-weight: 600;
}

.ww-form {
  background-color: #f9f9f9;
  border: 1px solid #ccc;
  border-radius: 4px;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
  display: flex;
}
